export default function IndexCard () {
  const classes = useStyles()

  const routerCtx = useRouter()
  const stateCtx = useGlobalState()
  const mutationCtx = useGlobalMutation()

  const handleClick = () => {
    if (!stateCtx.config.channelName) {
      mutationCtx.toastError('Enter your class room code')
      return
    }

    routerCtx.history.push({ pathname: `/meeting/${stateCtx.config.channelName}/host` })
  }

  return (
    <Box flex="1" display="flex" alignItems="center" justifyContent="flex-start" flexDirection="column">
      {/* <Link to="/setting" className='setting-btn' /> */}
      {/* <a href="https://github.com/AgoraIO/Advanced-Video/tree/master/Web/17-Multistream" className="github" /> */}
      <Box marginTop="92" flex="1" display="flex" alignItems="center" justifyContent="center" flexDirection="column">
        <FormControl className={clsx(classes.input, classes.grid)}>
          <InputLabel htmlFor="channelName">Class Room Name</InputLabel>
          <Input
            id="channelName"
            name="channelName"
            value={stateCtx.config.channelName}
            onChange={(evt) => {
              const PATTERN = /^[a-zA-Z0-9!#$%&()+\-:;<=.>?@[\]^_{}|~,\s]{1,64}$/
              const value = PATTERN.test(evt.target.value)
              if (value && evt.target.value.length < 64) {
                mutationCtx.updateConfig({ channelName: evt.target.value })
              } else {
                mutationCtx.updateConfig({ channelName: '' })
              }
            }}
          />
        </FormControl>
        <FormControl className={classes.grid}>
          <Button onClick={handleClick} variant="contained" color="primary" className={classes.button}>
            Join
          </Button>
        </FormControl>
      </Box>
      <Box textAlign="center" fontWeight="fontWeightRegular" className={classes.bottomStyle} style={{ color: 'grey' }} fontSize="h7.fontSize">Version: {AgoraRTC.VERSION}</Box>
    </Box>
  )
}
